body {
  background-color: #fff;
  margin: 0;
}

img {
  border-radius: 5%;
}

.contentDiv {
  background-color: #000;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

#detailsDiv {
  box-sizing: border-box;
  background: none;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  gap: 12px;
  height: 100vh;
  padding: 16px;
  display: flex;
}

#detailsDiv #trackName {
  text-align: center;
  margin: 0;
  padding: 4px 8px;
  font-size: 1.25rem;
  font-weight: 600;
}

#detailsDiv #lyricsDiv {
  -webkit-overflow-scrolling: touch;
  min-height: 0;
  color: inherit;
  border-radius: 8px;
  flex: auto;
  padding: 12px;
  overflow: auto;
  background-color: #0000 !important;
}

#detailsDiv #controlsDiv {
  box-sizing: border-box;
  background: none;
  flex: none;
  justify-content: center;
  align-self: stretch;
  align-items: center;
  width: 100%;
  height: 15dvh;
  margin-top: auto;
  padding: 8px;
  display: flex;
}

#detailsDiv #controlsList {
  justify-content: center;
  align-items: center;
  gap: 12px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

#detailsDiv #controlsList li {
  float: none;
}

#detailsDiv #controlsList button {
  color: #000;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 56px;
  font-size: 1.25rem;
  display: inline-flex;
}

#lyricsDiv {
  scrollbar-width: none;
  scroll-behavior: smooth;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  max-height: 60vh;
  padding: 1rem;
  display: flex;
  overflow-y: auto;
}

.line {
  color: #ffffff80;
  text-align: center;
  max-width: 90%;
  margin: .4rem 0;
  font-size: 1rem;
  line-height: 1.6;
  transition: all .4s;
}

.line.active {
  color: #fff;
  opacity: 1;
  font-size: 1.5rem;
  font-weight: 600;
  transform: scale(1.05);
}

.line.faded {
  color: #ffffffb3;
  font-size: 1.2rem;
}
